html,body,.pyg_view{
  height: 100%;
}
body{
  padding-top: 45px;
  padding-bottom: 45px;
}
.pyg_view{
  overflow: scroll;
  background-color: aqua;
}

.pyg_header{
  padding: 5px;
  width: 100%;
  height: 45px;
  position: fixed;
  background-color: #fff;
  top: 0;
  left: 0;
}
.pyg_footer{
  background-color: forestgreen;
  width: 100%;
  height: 45px;
  position: fixed;
  bottom: 0;
  left: 0;
}
